import request from '@/utils/request'

/* 生态红线调整审核列表(无分页) */
export const listShp = data => request({ url: '/ktdb/shpBatch/listShp', method: 'post', data })
/* 生态红线调整审核列表(有分页) */
export const getList = data => request({ url: '/ktdb/shpBatch/list', method: 'post', data })
/* 问题点审核 */
export const pointStageExamine = data => request({ url: '/ledger/pointStageExamine', method: 'post', data })
/* 红线调整审核获取详情 */
export const getShpDetailById = data => request({ url: '/ktdb/shpBatch/getShpDetailById', method: 'post', data })
/* 添加数据 */
export const addShp = data => request({ url: '/ktdb/shpBatch/addShp', method: 'post', data })
/* 删除数据 */
export const deleteShp = data => request({ url: '/ktdb/shpBatch/deleteShp', method: 'post', data })

