import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 切割工具 */
export default {
  data () {
    return {
      // 剪切工具状态
      cutstatus: false,

      // 为切割线创建图形图层
      cutGraphic1: {},
      cutGraphic2: {},
      // 定义切割线对象
      cutter: null,
      // 是否选中待裁剪区域的状态
      isSelected: false,
      // 开始绘制的开关
      disableDraw: true,
      // 管理用于绘制线条的地图单击
      initClick: {},
      clicks: 0,
      scores: []
    }
  },
  methods: {
    /* 切换剪切按钮状态 */
    cutStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('cut')
      if (!this.cutstatus) {
        let sLayer = this.map.findLayerById('sLayer')
        if (!sLayer.graphics.length) {
          setTimeout(() => {
            this.$Modal.warning({
              title: '选择',
              content: '请选择需要剪切的图形！'
            })
          }, 300)
        } else if (sLayer.graphics.length > 1) {
          setTimeout(() => {
            this.$Modal.error({
              title: '错误',
              content: '每次只能对一个图形进行剪切！'
            })
          }, 300)
        } else {
          this.cursorType = 'cut'
          this.cutstatus = true
          this.addCut()
        }
      } else {
        this.cutstatus = false
        this.delCut()
      }
    },
    /* 添加切割工具 */
    async addCut () {
      // 定义地图的点击事件
      this.cutClick = this.view.on('click', (evt) => {
        // 开始绘制切割线
        this.startCut(evt)
      })
      // 开始画线后，继续用光标移动绘制
      this.cutPointerMove = this.view.on('pointer-move', (evt) => {
        if (this.cutter && !this.disableDraw) {
          // 获取鼠标移动的坐标，需要用view的方法转换一下
          let point = this.view.toMap({ x: evt.x, y: evt.y })
          // console.log("鼠标移动，x:"+point.x+",   y:"+point.y);

          // 将缓存数组最后一个坐标修改为鼠标当前位置
          this.scores[this.scores.length - 1] = [point.x, point.y]
          // 深度复制cutter到新对象
          let cutterTmp = this.cutter.clone()
          // 修改坐标位置
          cutterTmp.geometry.paths = this.scores
          // 删除原始线条
          this.view.graphics.remove(this.cutter)
          // 添加新的裁剪线
          this.view.graphics.add(cutterTmp)
          // 记录坐标
          this.cutter = cutterTmp
          // 随着鼠标移动绘制剪切后的形状
          this.cutGeoms(this.originalSelectedGraphics[0].geometry, this.cutter.geometry, point, this.map.findLayerById('sLayer'))
        }
      })
      // 双击事件完成绘制线条
      this.cutDoubleClick = this.view.on('double-click', async (evt) => {
        if (!this.disableDraw) {
          // 暂时关闭双击放大地图的功能
          evt.stopPropagation()
          // 关闭绘制
          this.disableDraw = true
          // 清空绘制图层
          this.view.graphics.removeAll()
          // 按照鼠标双击的位置，再切一次图，返回截图状态
          let goodCut = this.cutGeoms(this.originalSelectedGraphics[0].geometry, this.cutter.geometry, evt.mapPoint, this.map.findLayerById('orchardLayer'))
          // 将父几何的属性赋值给两个切割得到的子几何
          this.cutGraphic1.attributes = this.originalSelectedGraphics[0].attributes
          this.cutGraphic2.attributes = this.originalSelectedGraphics[0].attributes
          // 获取地块面积和中心点
          await this.getAreaCenter(this.cutGraphic1)
          await this.getAreaCenter(this.cutGraphic2)
          if (goodCut) {
            // 将剪切图形添加到地图并删除原始选定图形
            this.map.findLayerById('orchardLayer').applyEdits({
              addFeatures: [this.cutGraphic1, this.cutGraphic2],
              deleteFeatures: this.originalSelectedGraphics
            })
          } else {
            // 关闭绘制
            this.disableDraw = true
          }
          // 管理工具清理
          this.clearSelectedFeatures()
          // 关闭剪切工具
          this.cutstatus = false
          this.delCut()
          // 打开选择工具
          this.activeChoice()
        }
      })
    },
    // 开始绘制切割线
    async startCut (evt) {
      let geometryEngine = await arcgisPackage.geometryEngine
      let Graphic = await arcgisPackage.Graphic
      // 记录当前点击的点的坐标数据，此处为全局变量
      this.initClick = evt.mapPoint
      if (this.clicks === 0) {
        // 第一次点击，初始化切割工具
        this.initCut()
      }
      this.clicks++ // 记录点击次数
      if (this.clicks === 1) {
        // 判断
        let initClickOutsideSelection = geometryEngine.disjoint(this.originalSelectedGraphics[0].geometry, this.initClick)
        console.log(initClickOutsideSelection)
        if (!initClickOutsideSelection) {
          this.clicks = 1
          // 不允许切割到所选特征的内部
          this.$Modal.warning({
            title: '注意',
            content: '在所选特征外开始切割线！'
          })
        } else {
          // 将初始坐标记录下来，用于后续鼠标移动时修改
          console.log(evt)
          this.scores = [[evt.mapPoint.x, evt.mapPoint.y], [evt.mapPoint.x, evt.mapPoint.y]]
          // 将初始位置设置为线，实例化Graphic并添加到地图
          let lineGeom = {
            type: 'polyline',
            paths: this.scores,
            spatialReference: config.spatialReference
          }
          this.cutter = new Graphic({
            geometry: lineGeom,
            symbol: config.cutterSym
          })
          this.view.graphics.add(this.cutter)
          // console.log("第一次点击，x:"+evt.mapPoint.x+",   y:"+evt.mapPoint.y);
        }
      } else if (this.clicks > 1) {
        // 为每次点击添加额外的线路路径（有时可能是“错误的”）不要点击太快。
        this.scores[this.scores.length] = [evt.mapPoint.x, evt.mapPoint.y]
      }
    },
    // 第一次点击，初始化切割工具
    initCut () {
      this.cutter = null // 每次切割初始化时，将切割机重置为空。
      this.disableDraw = false // 启用绘图
      this.scores = null
    },
    // 用地图上画的线切割多边形     选中的面        裁剪的线条        鼠标当前位置      选中面的图层
    async cutGeoms (poly, cutLine, endPoint, lyr) {
      let geometryEngine = await arcgisPackage.geometryEngine
      let Graphic = await arcgisPackage.Graphic
      // 判断一个几何图形是否与另一个几何图形交叉。判断裁剪的线和选中的面交叉
      let crossTest = geometryEngine.crosses(cutLine, poly)
      // 判断当前鼠标位置不在选中的面中
      let disjointTest = geometryEngine.disjoint(poly, endPoint)
      // 如果线条没有正确交叉选定的几何图形，则不要尝试剪切。
      if (crossTest && disjointTest) {
        // 在输入多段线或多边形与切割多段线相交的位置拆分输入多段线或多边形。
        let cutPolys = geometryEngine.cut(poly, cutLine)
        if (lyr === this.map.findLayerById('sLayer')) {
          lyr.removeAll()
          // 定义最终剪切图形，并在鼠标移动时将临时图形添加到地图中
          this.cutGraphic1 = new Graphic({ geometry: cutPolys[0], symbol: config.orchardSym })
          this.cutGraphic2 = new Graphic({ geometry: cutPolys[1], symbol: config.orchardSym })
          lyr.add(new Graphic({ geometry: cutPolys[0], symbol: config.selectionSym }))
          lyr.add(new Graphic({ geometry: cutPolys[1], symbol: config.selectionSym }))
          geometryEngine.offset(cutPolys[0], 10, 9002, 0)
          lyr.add(new Graphic({ geometry: geometryEngine.offset(cutPolys[0], 10, 9002, 0), symbol: config.offsetSym }))
          lyr.add(new Graphic({ geometry: geometryEngine.offset(cutPolys[1], 10, 9002, 0), symbol: config.offsetSym }))
        }
        if (lyr === this.map.findLayerById('orchardLayer')) {
          // 完成生产线
          this.map.findLayerById('sLayer').removeAll()
        }
        return true
      } else {
        return false
      }
    },
    // 用于管理清除的选择
    clearSelectedFeatures () {
      this.map.findLayerById('sLayer').removeAll()
      this.isSelected = false
      this.clicks = 0
      this.cutter = null
      this.scores = null
    },
    /* 关闭剪切工具 */
    async delCut () {
      let removes = (rem) => {
        if (rem) {
          rem.remove()
          rem = undefined
        }
      }
      removes(this.cutClick)
      removes(this.cutPointerMove)
      removes(this.cutDoubleClick)
    }
  }
}
