import choiceIco from '../ico/choice.ico'
import cutIco from '../ico/cut.ico'
import choiceAddIco from '../ico/choiceAdd.ico'
import choiceReduceIco from '../ico/choiceReduce.ico'
import annoIco from '../ico/anno.ico'
/* 鼠标样式修改 */
export default {
  data () {
    return {
      cursorType: 'default',
      referCursorType: 'default'
    }
  },
  watch: {
    cursorType (val) {
      switch (val) {
        case 'default':
          this.$refs.viewDiv.style.cursor = 'default'
          break
        case 'choice':
          this.$refs.viewDiv.style.cursor = 'url(' + choiceIco + ') 6 3,pointer'
          break
        case 'choiceAdd':
          this.$refs.viewDiv.style.cursor = 'url(' + choiceAddIco + ') 6 3,pointer'
          break
        case 'choiceReduce':
          this.$refs.viewDiv.style.cursor = 'url(' + choiceReduceIco + ') 6 3,pointer'
          break
        case 'cut':
          this.$refs.viewDiv.style.cursor = 'url(' + cutIco + ') 6 3,pointer'
          break
        case 'anno':
          this.$refs.viewDiv.style.cursor = 'url(' + annoIco + ') 6 3,pointer'
          break
        default:
          break
      }
    },
    referCursorType (val) {
      switch (val) {
        case 'default':
          this.$refs.referViewDiv.style.cursor = 'default'
          break
        case 'choice':
          this.$refs.referViewDiv.style.cursor = 'url(' + choiceIco + ') 6 3,pointer'
          break
        case 'choiceAdd':
          this.$refs.referViewDiv.style.cursor = 'url(' + choiceAddIco + ') 6 3,pointer'
          break
        case 'choiceReduce':
          this.$refs.referViewDiv.style.cursor = 'url(' + choiceReduceIco + ') 6 3,pointer'
          break
        case 'cut':
          this.$refs.referViewDiv.style.cursor = 'url(' + cutIco + ') 6 3,pointer'
          break
        case 'anno':
          this.$refs.referViewDiv.style.cursor = 'url(' + annoIco + ') 6 3,pointer'
          break
        default:
          break
      }
    }
  },
  matched: {
  }
}
