<template>
  <div class="map-box">
    <!-- 地图dom，分屏 -->
    <div class="viewDoms">
      <Split v-model="split" style="height: 100%" min="400px" max="480px" :style="{ width: split ? '100%' : 'calc(100% + 6px)', marginLeft: split ? '0' : '-6px' }">
        <div slot="left" class="splitPane">
          <div ref="referViewDiv" id="referViewDiv">
          </div>
        </div>
        <div slot="right" class="splitPane">
          <div ref="viewDiv" id="viewDiv">
          </div>
        </div>
      </Split>
    </div>
    <!-- 保存、取消-->
    <p class="btns">
<!--      <Button type="success" size="small" class="btn-save mr15"  @click="saveLayer0()"><i class="iconfont icon-Save"></i>保存</Button>-->
      <Button type="primary" size="small" @click="$emit('signOut',false)" >返回</Button>
    </p>
    <!-- 图层控制工具 -->
    <div id="layerControl" class="layerControl">
      <button class="annoBtn" @click="layerPanelStatus = !layerPanelStatus" style="border-radius: 9px;display: none">
        <i class="iconfont">&#xe613;</i>
      </button>
      <!--图层面板-->
      <div class="layerPanel" v-show = 'layerPanelStatus'>
        <ul>
          <CheckboxGroup v-model="backSocial" @on-change="backSocialChange">
            <!--基年数据选项-->
            <li class="backLayer">
              <Checkbox label="backLayer">&nbsp;</Checkbox>
              <p @click="backListStatus = !backListStatus" style="margin-left: 29px;margin-top: -20px">
                基年数据图层
                <span style="float: right" v-show="!backListStatus"><i class="iconfont">&#xe6aa;</i></span>
                <span style="float: right" v-show="backListStatus"><i class="iconfont">&#xe6a9;</i></span>
              </p>
              <!--基年数据面板-->
              <div class="backListPanel" v-show="backListStatus">
                <ul>
                  <li v-show="!backList.length">没有基年数据...</li>
                  <RadioGroup v-model="selectBackId">
                    <li v-for="item in backList" :key="item.ab001">
                      <Radio :label="item.ab001"><span>{{ item.ab008 }}</span></Radio>
                    </li>
                  </RadioGroup>
                </ul>
              </div>
            </li>
            <li v-if="$route.query.userType * 1 !== 0"><Checkbox label="examLayer">&nbsp;</Checkbox>批注图层</li>
          </CheckboxGroup>
        </ul>
      </div>
    </div>
    <!-- 参考屏图层控制工具 -->
    <div id="referLayerControl" class="layerControl">
      <button class="annoBtn" @click="referPanelStatus = !referPanelStatus" style="border-radius: 10px">
        <i class="iconfont">&#xe613;</i>
      </button>
      <!--图层面板-->
      <div class="layerPanel"  v-show = 'referPanelStatus'>
        <ul>
          <CheckboxGroup v-model="referbackSocial" @on-change="backSocialChangeRefer">
            <!--基年数据选项-->
            <li class="backLayer">
              <Checkbox label="backLayer">&nbsp;</Checkbox>
              <p @click="referbackListStatus = !referbackListStatus" style="margin-left: 29px;margin-top: -20px">
                基年数据图层
                <span style="float: right" v-show="!referbackListStatus"><i class="iconfont">&#xe6aa;</i></span>
                <span style="float: right" v-show="referbackListStatus"><i class="iconfont">&#xe6a9;</i></span>
              </p>
              <!--基年数据面板-->
              <div class="backListPanel" v-show="referbackListStatus">
                <ul>
                  <li v-show="!backList.length">没有基年数据...</li>
                  <RadioGroup v-model="referselectBackId">
                    <li v-for="item in backList" :key="item.ab001">
                      <Radio :label="item.ab001"><span>{{ item.ab008 }}</span></Radio>
                    </li>
                  </RadioGroup>
                </ul>
              </div>
            </li>
          </CheckboxGroup>
        </ul>
      </div>
    </div>
    <!-- 地图放大控制 -->
    <div id="zoomIn">
      <button class="btnTools" @click="zoomIn">
        <i class="iconfont">&#xe607;</i>
      </button>
    </div>
    <!-- 地图缩小控制 -->
    <div id="zoomOut">
      <button class="btnTools" @click="zoomOut">
        <i class="iconfont">&#xe60e;</i>
      </button>
    </div>
    <!-- 辅助屏地图放大控制 -->
    <div id="referZoomIn">
      <button class="btnTools" @click="zoomIn">
        <i class="iconfont">&#xe607;</i>
      </button>
    </div>
    <!-- 辅助屏地图缩小控制 -->
    <div id="referZoomOut">
      <button class="btnTools" @click="zoomOut">
        <i class="iconfont">&#xe60e;</i>
      </button>
    </div>
    <!-- 分屏工具 -->
    <div id="referBtn">
      <!-- 分屏工具条 -->
      <div class="tools" style="padding: 1px;border-radius: 9px;display: none">
        <button  @click.stop="referStateToggle" :class="{ active : referStatus }" title="分屏" style="border-radius: 9px;outline: none">
          <i class="iconfont">&#xe68b;</i>
        </button>
      </div>
    </div>
    <!-- 工具条 -->
    <div id="toolbar" v-show="$route.query.userType * 1 !==3">
      <!-- 业务工具条 -->
      <div class="tools" style="display: none">
        <button @click.stop="IntersectError(1)" title="保存"  v-if="$route.query.userType * 1 !==3">
          <i class="iconfont">&#xe629;</i>
        </button>
        <!--<button @click.stop="IntersectError(2)" title="保存并提交"  v-if="$route.query.userType * 1 === 0 || $route.query.userType * 1 === 1">
          <i class="iconfont">&#xe629;</i>
        </button>
        <button @click.stop="IntersectError(3)" title="保存并驳回"  v-if="$route.query.userType * 1 === 2">
          <i class="iconfont">&#xe629;</i>
        </button>
        <button @click.stop="IntersectError(4)" title="保存并通过"  v-if="$route.query.userType * 1 === 2">
          <i class="iconfont">&#xe629;</i>
        </button>-->
        <router-link :to="{ name:'needDo'}" v-if="$route.query.userType * 1 ===0 ||$route.query.userType * 1 ===3 || $route.query.userType * 1 ===1">
          <button title="返回">
            <i class="iconfont">&#xe604;</i>
          </button>
        </router-link>
        <router-link :to="{ name:'examDo'}" v-if="$route.query.userType * 1 ===2 && $route.query.personType * 1 === 2">
          <button title="返回">
            <i class="iconfont">&#xe604;</i>
          </button>
        </router-link>
        <router-link :to="{ name:'checkPage', query: {taskId: $route.query.taskId } }" style="margin-right: 10px" v-if="$route.query.userType * 1 ===2 && $route.query.personType * 1 === 3">
          <button title="返回">
            <i class="iconfont">&#xe604;</i>
          </button>
        </router-link>
      </div>
      <!-- 操作工具条 -->
<!--      <div class="tools" v-show="orchardToolsStatus">-->
<!--        &lt;!&ndash;<button  @click.stop="cutStateToggle">停靠</button>&ndash;&gt;-->
<!--        <button @click.stop="choiceStateToggle" :class="{ active : choiceStatus }" title="选择">-->
<!--          <i class="iconfont">&#xe6ed;</i>-->
<!--        </button>-->
<!--        <button @click.stop="cutStateToggle" :class="{ active : cutstatus }" title="剪切">-->
<!--          <i class="iconfont">&#xe6f6;</i>-->
<!--        </button>-->
<!--        <button @click.stop="addStateToggle" :class="{ active : addStatus }" title="新建">-->
<!--          <i class="iconfont">&#xe66a;</i>-->
<!--        </button>-->
<!--        <button @click.stop="mergeStateToggle" :class="{ active : mergeStatus }" title="融合">-->
<!--          <i class="iconfont">&#xe683;</i>-->
<!--        </button>-->
<!--        <button @click.stop="intersectStateToggle" :class="{ active : intersectStatus }" title="交集">-->
<!--          <i class="iconfont">&#xe682;</i>-->
<!--        </button>-->
<!--        <button @click.stop="scatterClick" title="打散">-->
<!--          <i class="iconfont">&#xe64b;</i>-->
<!--        </button>-->
<!--        <button @click.stop="attributeStateToggle" :class="{ active : attributeStatus }" title="属性操作">-->
<!--          <i class="iconfont">&#xe6fc;</i>-->
<!--        </button>-->
<!--        <button @click.stop="deleteClick" title="删除">-->
<!--          <i class="iconfont">&#xe714;</i>-->
<!--        </button>-->
<!--        <button @click.stop="annoStatusToggle" :class="{ active : annoStatus }" title="批注"  v-show="examToolsStatus&&$route.query.userType * 1 !== 0 ">-->
<!--          <i class="iconfont">&#xe95f;</i>-->
<!--        </button>-->
<!--      </div>-->
    </div>
    <!-- 基年数据工具条 -->
    <div id="orcharToolbar">
      <!-- 基年数据工具条 -->
      <div class="tools">
        <button  @click.stop="referChoiceStateToggle" :class="{ active : referChoiceStatus }" title="选择">
          <i class="iconfont">&#xe797;</i>
        </button>
        <button @click.stop="copyClick" title="将选中几何图形复制到业务图层">
          <i class="iconfont">&#xe63f;</i>
        </button>
        <button @click.stop="loadClick" title="将所有几何图形复制到业务图层">
          <i class="iconfont">&#xe6ce;</i>
        </button>
      </div>
    </div>
    <!--融合属性选择-->
    <div id="mergeModal" class="panel" v-show="mergeModal">
      <div class="attributeList">
        <ul>
          <li v-for="(item, index) in originalSelectedGraphics" :key="index"
              :class="{
              attrLiActive: item.attributes.ObjectID === mergeObj.ObjectID,
              attrCopyActive: item.attributes.ObjectID === mergeObj.ObjectID
              }"
              @click="mergeChickList(item.attributes)"
          >
            <span>id：</span><span>{{item.attributes.ObjectID}}</span>
          </li>
        </ul>
      </div>
      <div class="attributeDetail">
        <table border="0" cellspacing="1" cellpadding="0" class="tab">
          <thead>
          <tr>
            <th style="width: 50%">属性</th>
            <th>值</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item, index) in mergeObj" :key="index">
            <td>{{index | nameToAlias}}</td>
            <td>{{item}}</td>
          </tr>
          </tbody>
        </table>
      </div>
      <p style="text-align: right">
        <button class="btn" @click="mergeGeoms" :class="{btnDisable: JSON.stringify(mergeObj) === '{}'}">融合</button>
      </p>
    </div>
    <!--交集面板-->
    <div id="intersectModal" class="panel" v-show="intersectModal"><!--intersectModal-->
      <ul>
        <li @mouseover="intersectAttrOver('left')" @mouseout="intersectAttrOut" @click="intersectGeoms" v-show="intersectType === 'intersect'">左切</li>
        <li @mouseover="intersectAttrOver('right')" @mouseout="intersectAttrOut" @click="intersectGeoms" v-show="intersectType === 'intersect'">右切</li>
        <li :class="{ panelActive: intersectPanelType === 'center' }" @mouseover="intersectAttrOver('center')" @mouseout="intersectAttrOut" @click="openIntersectAttrPanel('center')" v-show="intersectType === 'intersect'">交集</li>
        <li :class="{ panelActive: intersectPanelType === 'all' }" @mouseover="intersectAttrOver('all')" @mouseout="intersectAttrOut" @click="openIntersectAttrPanel('all')" v-show="intersectType === 'intersect'">切割</li>
        <li @mouseover="intersectAttrOver('contains')" @mouseout="intersectAttrOut" @click="intersectGeoms" v-show="intersectType === 'contains'">抠图</li>
      </ul>
    </div>
    <!--交集属性选择-->
    <div id="intersectPanel" class="panel" v-show="intersectPanel">
      <p>请为交集部分选择属性模板：</p>
      <div class="attributeList">
        <ul>
          <li v-for="(item, index) in originalSelectedGraphics" :key="index"
              :class="{
              attrLiActive: item.attributes.ObjectID === intersectObj.ObjectID,
              attrCopyActive: item.attributes.ObjectID === intersectObj.ObjectID
              }"
              @click="intersectChickList(item.attributes)"
          >
            <span>id：</span><span>{{item.attributes.ObjectID}}</span>
          </li>
        </ul>
      </div>
      <div class="attributeDetail">
        <table border="0" cellspacing="1" cellpadding="0" class="tab">
          <thead>
          <tr>
            <th style="width: 50%">属性</th>
            <th>值</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item, index) in intersectObj" :key="index">
            <td>{{index| nameToAlias}}</td>
            <td>{{item}}</td>
          </tr>
          </tbody>
        </table>
      </div>
      <p style="text-align: right">
        <button v-show="intersectPanelType === 'center'" class="btn" @click="intersectGeoms('center')" :class="{btnDisable: JSON.stringify(intersectObj) === '{}'}">
          交集
        </button>
        <button v-show="intersectPanelType === 'all'" class="btn" @click="intersectGeoms('all')" :class="{btnDisable: JSON.stringify(intersectObj) === '{}'}">
          切割
        </button>
      </p>
    </div>
    <!--属性面板-->
    <div id="attributePanel" class="panel" v-show="attributeStatus">
      <button class="btn" :class="{btnDisable: JSON.stringify(attributeCopy) === '{}'}" @click="attrCover">覆盖</button>
      <div class="attributeList">
        <p v-show="!originalSelectedGraphics.length" style="text-align: center;line-height: 80px">请先选择几何</p>
        <ul>
          <li v-for="(item, index) in originalSelectedGraphics" :key="index"
              :class="{
              attrLiActive: item.attributes.ObjectID === attributeObj.ObjectID,
              attrCopyActive: item.attributes.ObjectID === attributeCopy.ObjectID
              }"
              @click="attrChickList(item.attributes)"
          >
            <span>id：</span><span>{{item.attributes.ObjectID}}</span>
            <i class="iconfont" title="删除" @click.stop="attrDelchoice(item.attributes.ObjectID)">&#xe605;</i>
            <i class="iconfont" title="粘贴" @click.stop="attrPaste(item.attributes.ObjectID)"
               :class="{unActive: JSON.stringify(attributeCopy) === '{}'}"
            >&#xe95e;</i>
            <i class="iconfont" title="复制" @click.stop="attrCopy(item.attributes)">&#xe701;</i>
          </li>
        </ul>
      </div>
      <div class="attributeDetail">
        <button class="btn" @click="attrEdit" :class="{btnDisable: JSON.stringify(attributeObj) === '{}'}">编辑</button>
        <button class="btn" @click="attrConserve"
                :class="{btnDisable: !(JSON.stringify(attributeObj) !== '{}'&&attributeEdit)}"
        >保存</button>
        <table border="0" cellspacing="1" cellpadding="0" class="tab">
          <thead>
          <tr>
            <th style="width: 50%">属性</th>
            <th>值</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>{{'ObjectID' | nameToAlias}}</td>
            <td ref="ObjectIDRef"></td>
          </tr>
          <tr>
            <td>{{'name' | nameToAlias}}</td>
            <td ref="puctRef" v-show="!attributeEdit"></td>
            <td ref="typeRefName" v-if="attributeEdit" style="padding: 0">
              <Input v-model="nameValue" size="small" style="width: 100%"></Input>
            </td>
          </tr>
          <tr>
            <td>{{'type' | nameToAlias}}</td>
            <td ref="typeRef" v-show="!attributeEdit"></td>
            <td ref="typeRefEdit" v-if="attributeEdit" style="padding: 0">
              <Select v-model="typeValue" size="small" style="width:100%">
                <Option value="农业用地">农业用地</Option>
                <Option value="居民点">居民点</Option>
                <Option value="工矿用地">工矿用地</Option>
                <Option value="采石场">采石场</Option>
                <Option value="能源设施">能源设施</Option>
                <Option value="旅游设施">旅游设施</Option>
                <Option value="交通设施">交通设施</Option>
                <Option value="养殖场">养殖场</Option>
                <Option value="道路">道路</Option>
                <Option value="其他人工设施">其他人工设施</Option>
              </Select>
            </td>
          </tr>
          <tr>
            <td>{{'region' | nameToAlias}}</td>
            <td ref="regionRef" v-show="!attributeEdit"></td>
            <td ref="regionRefEdit" v-if="attributeEdit" style="padding: 0">
              <Select v-model="regionValue" size="small" style="width:100%">
                <Option value="核心区">核心区</Option>
                <Option value="缓冲区">缓冲区</Option>
                <Option value="试验区">试验区</Option>
              </Select>
            </td>
          </tr>
          <tr>
            <td>{{'position' | nameToAlias}}</td>
            <td ref="positionRef" v-show="!attributeEdit"></td>
            <td ref="positionRefEdit" v-if="attributeEdit" style="padding: 0">
              <Input v-model="positionValue" size="small"/>
            </td>
          </tr>
          <tr>
            <td>{{'area' | nameToAlias}}</td>
            <td ref="areaRef"></td>
          </tr>
          <tr>
            <td>{{'center' | nameToAlias}}</td>
            <td ref="centerRef"></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- 审核标注面板 -->
    <div id="annoPanel" class="panel" v-show="annoStatus">
      <div class="annoList">
        <p v-show="!changeData.length && !pointData.length" style="text-align: center;line-height: 80px">暂无批注点</p>
        <ul>
          <li v-for="(item, index) in pointData" :key="'point' + index" class="clr">
            <i class="point" style="background: red"></i>
            <p v-if="item.attributes.annotat" @click="pointAnnoLi(item, 'point')"
               :class="{
              attrActive: item.attributes.ObjectID === annoActive.attributes.ObjectID && annoActive.attributes.type === 'point'
              }">{{item.attributes.annotat}}</p>
            <p v-if="!item.attributes.annotat" @click="pointAnnoLi(item, 'point')"
               :class="{
              attrActive: item.attributes.ObjectID === annoActive.attributes.ObjectID && annoActive.attributes.type === 'point'
              }">{{item.geometry.x}},{{item.geometry.y}}</p>
            <span class="iconfont" >
              <i class="icon" title="编辑" @click.stop="pointAnnoLi(item, 'point');editAnnotatLi(item)" v-if=" $route.query.userType * 1 !== 1 ">&#xe95f;</i>
              <i class="icon" title="删除" @click.stop="annoDelete(item)" v-if=" $route.query.userType * 1 !== 1 ">&#xe605;</i>
              <i class="icon" title="确认修改" @click.stop="editAnnoStatus(item)" v-if=" $route.query.userType * 1 === 1">&#xe603;</i>
            </span>
            <Input type="textarea" v-model="annoCon" :rows="3" placeholder="请填写批注" style="margin: 5px 0 5px 20px;width:calc(100% - 20px)"
                   v-if="item.attributes.ObjectID === annoActive.attributes.ObjectID && annoActive.attributes.type === 'point'&&listInputStatus"
            />
          </li>
          <li v-for="(item, index) in changeData" :key="'change' + index" class="clr" @click="pointAnnoLi(item, 'change')">
            <i class="point" style="background: #19be6b"></i>
            <p v-if="item.attributes.annotat"
               :class="{
              attrActive: item.attributes.ObjectID === annoActive.attributes.ObjectID && annoActive.attributes.type === 'change'
              }">{{item.attributes.annotat}}</p>
            <p v-if="!item.attributes.annotat"
               :class="{
              attrActive: item.attributes.ObjectID === annoActive.attributes.ObjectID && annoActive.attributes.type === 'change'
              }">{{item.geometry.x}},{{item.geometry.y}}</p>
          </li>
        </ul>
      </div>
      <button class="btn" @click="addCnno" v-if=" $route.query.userType * 1 !== 1 ">新增错误点</button>
    </div>
  </div>
</template>
<script>
import create from '@/components/method/create'
import init from '@/components/maptool/init'
import anno from '@/components/maptool/anno'
import config from '@/components/config'
import refer from '@/components/maptool/refer'
import layerControl from '@/components/maptool/layerControl'
import referLayerControl from '@/components/maptool/referLayerControl'
import { saveInterpretationResult, getGeoJson } from '@/api/remoteSensing'
import arcgisPackage from '../../utils/arcgisTools'

export default {
  name: 'adjustmentMap',
  mixins: [create, init, refer, layerControl, referLayerControl, anno],
  data () {
    return {
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      layerCon: false, // 控制图层控制器开关
      type: '', // 正在使用的测量工具标识
      orchardToolsStatus: true,
      userType: 0,
      configSocial: ['orchardLayer', 'backLayer', 'examLayer'], // 图层控制面板的id数组
      featureData: [] // 获取到的要素数据
    }
  },
  computed: {},
  props: {},
  watch: {},
  async mounted() {
    /* 创建地图 */
    await this.createMap()
    /* 创建参考地图 */
    await this.createReferMap()
    /* 添加小工具 */
    await this.addWidgets()
    /* 添加工具图层 */
    await this.addAllToolLayers(this.map)
    await this.addReferToolLayers(this.refermap)
    /* 添加自定义工具 */
    await this.addTools()
    // 分屏按鈕
    this.view.ui.add(document.getElementById('referBtn'), 'top-left')
    // 图层控制面板
    this.view.ui.add(document.getElementById('layerControl'), 'top-left')
    // 图层控制面板
    this.referview.ui.add(document.getElementById('referLayerControl'), 'top-left')

    /* 获取基年数据列表 */
    await this.getBackList()
    await this.getGeoJson()
  },
  methods: {
    /**
     * 初始化组件
     * */
    initCom(id) {
      /* 获取业务数据 */
      // this.getGeoJson()
      //this.addLayer('http://120.24.69.160:6080/arcgis/rest/services/jshx/testJY/MapServer');
      //this.addLayer('http://120.24.69.160:6080/arcgis/rest/services/jshx/testJYResult/MapServer');
    },
    /* 获取图层数据 */
    async getGeoJson () {
      // this.layerAddData()
      /* await getGeoJson({
        id: this.$parent.activeId
      }).then((res) => {
        if (res.data.code === '0000') {
          this.layerAddData(res.data)
          // this.getAnnoListData()
        } else {
          this.$Notice.error({title: '获取解译数据失败', desc: res.msg})
        }
      }) */
    },
    async addLayer (url) {
      let MapImageLayer = await arcgisPackage.MapImageLayer
      let mapimagelayer = new MapImageLayer({
        url: url
      })

      this.map.add(mapimagelayer)
      mapimagelayer.on('layerview-create', (event) => {
        this.view.extent = mapimagelayer.fullExtent.expand(1.4) // 定位图层
      })
    },
    /* 将获取到的数据添加到图层中 */
    async layerAddData () {
      let featureData = [{
        geometry: {
          spatialReference: config.spatialReference,
          rings: [
            [
              [12594736.948163297, 4340893.572568199],
              [13008108.397129422, 4225932.282027324],
              [12932282.865070548, 3731843.3311920734],
              [12391720.201037923, 3912846.2141713235],
              [12589844.978353048, 4321325.693327199],
              [12594736.948163297, 4340893.572568199]
            ]
          ],
          type: 'polygon'
        },
        attributes: {
          'ObjectID': 2,
          'name': null,
          'type': null,
          'region': null,
          'position': null,
          'area': '16353431.46',
          'center': '12699914°17′56.70″,4036368°27′6.77″'
        }
      }]
      this.map.findLayerById('orchardLayer').queryFeatures().then((res) => {
        this.map.findLayerById('orchardLayer').applyEdits({
          addFeatures: featureData
        })
      })
    },
    saveLayer0 () {
      this.$emit('signOut', false)
      this.$Notice.info({ title: '保存成功！' })
      /* this.map.findLayerById('orchardLayer').queryFeatures().then((layer) => {
        console.log(layer.features)
        console.log(JSON.stringify(layer.features))
        saveInterpretationResult({
          data: {
