            // todo 用户id
            createBy: 0
          }
        }).then((res) => {
          if (res.data.code === '0000') {
            this.$Notice.info({title: '人类活动标绘结果保存成功！'})
          } else {
            this.$Notice.error({title: res.msg})
          }
        })
      }) */
    },
    async addFeatureLayer (url) {
      console.log(url)
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let renderer = {
        type: 'simple',
        symbol: {
          type: 'simple-fill',
          color: [ 255, 0, 0, 0 ],
          outline: {
            width: 2,
            color: 'red'
          }
        }
      }
      // 图层数据查看
      let featureLayer = new FeatureLayer({
        url: url,
        renderer: renderer
      })

      this.map.add(featureLayer)
      featureLayer.on('layerview-create', (event) => {
        this.view.extent = featureLayer.fullExtent.expand(1.4) // 定位图层
      })

    }
  }
}
