import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 加载 */
export default {
  data () {
    return {
    }
  },
  methods: {
    async loadClick () {
      this.$Modal.confirm({
        title: '确认',
        content: '<p>加载基年数据将覆盖当前业务数据，确定加载么？</p>',
        onOk: () => {
          this.refermap.findLayerById('backLayer').queryFeatures().then((bres) => {
            this.map.findLayerById('orchardLayer').queryFeatures().then((ores) => {
              this.map.findLayerById('orchardLayer').applyEdits({
                addFeatures: bres.features,
                deleteFeatures: ores.features
              })
              this.openOrchar()
            })
          })
        }
      })
    }
  }
}
