import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 关闭 */
export default {
  data () {
    return {
      closeStatus: false
    }
  },
  methods: {
    closeStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('close')
      if (!this.closeStatus) {
        // 隐藏业务图层
        this.map.findLayerById('orchardLayer').visible = false
        this.map.findLayerById('sLayer').removeAll()
        // 选择工具修改为选择基年数据图层要素
        this.choiceLayerId = 'backLayer'
        this.closeStatus = true
      } else {
        this.openOrchar()
      }
    },
    /* 打开业务图层 */
    openOrchar () {
      // 展示业务图层
      this.map.findLayerById('orchardLayer').visible = true
      this.map.findLayerById('sLayer').removeAll()
      // 选择工具修改为选择业务数据图层要素
      this.choiceLayerId = 'orchardLayer'
      this.closeStatus = false
    }
  }
}
