/* 双屏 */
export default {
  data () {
    return {
      referStatus: false,
      split: 0
    }
  },
  methods: {
    /* 切换双屏按钮状态 */
    async referStateToggle () {
      if (!this.referStatus) {
        this.referStatus = true
        this.split = 0.5
        window.setTimeout(() => {
          this.referview.extent = this.view.extent
        }, 1000)
      } else {
        this.referStatus = false
        this.split = 0
      }
    }
  }
}
