import arcgisPackage from '@/utils/arcgisTools'
import config from '../config'
/* 交集 */
export default {
  data () {
    return {
      intersectStatus: false,
      intersectModal: false,
      intersectType: 'left',
      intersectSelections: [],
      intersectPanel: false, // 交集属性面板开关
      intersectPanelType: '', // 交集属性面板是交集还是切割  值为：center或all
      intersectObj: [] // 选中几何的属性详情
    }
  },
  methods: {
    /* 切换按钮状态 */
    intersectStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('intersect')
      if (!this.intersectStatus) {
        this.addIntersect()
      } else {
        this.map.findLayerById('sLayer').removeAll()
        this.delIntersect()
      }
    },
    /* 判断工具打开条件，通过后打开面板 */
    async addIntersect () {
      let geometryEngine = await arcgisPackage.geometryEngine
      let sLayer = this.map.findLayerById('sLayer')
      if (!sLayer.graphics.length) {
        setTimeout(() => {
          this.$Modal.warning({
            title: '选择',
            content: '请选择需要交集的图形！'
          })
          // 打开选择工具
          this.activeChoice()
        }, 300)
      } else if (sLayer.graphics.length !== 2) {
        setTimeout(() => {
          this.$Modal.warning({
            title: '选择',
            content: '请选择两个图形进行操作！'
          })
          // 打开选择工具
          this.activeChoice()
        }, 300)
      } else {
        let gras = this.map.findLayerById('sLayer').graphics.items
        /* 获取交集几何 */
        let contains1 = geometryEngine.contains(gras[0].geometry, gras[1].geometry)
        let contains2 = geometryEngine.contains(gras[1].geometry, gras[0].geometry)
        if (contains1 || contains2) {
          this.intersectType = 'contains'
          // 打开面板
          this.intersectModal = true
          this.intersectStatus = true
        } else {
          let intersect = geometryEngine.intersect(gras[1].geometry, gras[0].geometry)
          if (intersect) {
            this.intersectType = 'intersect'
            // 打开面板
            this.intersectModal = true
            this.intersectStatus = true
          } else {
            this.$Modal.warning({
              title: '选择',
              content: '请选择两个相交的图形进行操作！'
            })
            this.choiceStatus = false
          }
        }
      }
    },
    /* 工具关闭初始化 */
    delIntersect () {
      this.intersectStatus = false
      // 关闭面板
      this.intersectModal = false
      // 关闭属性面板
      this.intersectPanel = false
      // 初始化属性面板Type
      this.intersectPanelType = ''
    },
    /* 生成交集 */
    async intersectGeoms (type) {
      let gras = this.map.findLayerById('sLayer').graphics.items
      let arr = []
      for (let item of gras) {
        let gra = item.clone()
        arr.push(gra)
      }
      switch (type) {
        case 'center':
          arr[0].attributes = this.intersectObj
          break
        case 'all':
          arr[1].attributes = this.intersectObj
          break
        default:
          break
      }
      // 获取地块面积和中心点
      for (let i in arr) {
        await this.getAreaCenter(arr[i])
      }
      this.map.findLayerById('orchardLayer').applyEdits({
        addFeatures: arr,
        deleteFeatures: this.originalSelectedGraphics
      })
      /* 清空选择层 */
      this.map.findLayerById('sLayer').removeAll()
      // 关闭功能选择面板
      this.intersectModal = false
      /* 关闭其他工具 */
      this.closeOtherTools()
      // 打开选择工具
      this.activeChoice()
      // 关闭按钮
      this.intersectStatus = false
      // 关闭交集属性面板
      this.intersectPanel = false
    },
    /* 鼠标移入 */
    async intersectOver (type) {
      let Graphic = await arcgisPackage.Graphic
      let geometryEngine = await arcgisPackage.geometryEngine
      let sLayer = this.map.findLayerById('sLayer').graphics.items
      let geom1 = sLayer[0].geometry
      let geom2 = sLayer[1].geometry
      let attr1 = sLayer[0].attributes
      let attr2 = sLayer[1].attributes
      /* 获取交集几何 */
      let intersectGeometry = geometryEngine.intersect(geom1, geom2)
      /* 获取对斥几何 */
      let symmetricDifferenceGeometry = geometryEngine.symmetricDifference(geom1, geom2)
      /* 获取左切 */
      let differenceGeometryLeft = geometryEngine.difference(geom1, geom2)
      /* 获取右切 */
      let differenceGeometryRight = geometryEngine.difference(geom2, geom1)
      /* 将对斥几何拆分为左右两个几何 */
      let symmetricDifferenceGeometrys = []
      for (let item of symmetricDifferenceGeometry.rings) {
        let geometry = {
          type: symmetricDifferenceGeometry.type,
          rings: item,
          spatialReference: config.spatialReference
        }
        symmetricDifferenceGeometrys.push(geometry)
      }
      /* 清空选择层 */
      this.map.findLayerById('sLayer').removeAll()
      let arr = []
      let osGraphics1 = this.originalSelectedGraphics[1].clone()
      osGraphics1.symbol = config.intersectSym
      let osGraphics0 = this.originalSelectedGraphics[0].clone()
      osGraphics0.symbol = config.intersectSym
      /* 通过modal的判断修改数据层 */
      switch (type) {
        case 'left':
          arr = [
            new Graphic({ geometry: differenceGeometryLeft, symbol: config.intersectSym, attributes: attr1 }),
            osGraphics1
          ]
          this.map.findLayerById('sLayer').addMany(arr)
          break
        case 'center':
          arr = [
            new Graphic({ geometry: intersectGeometry, symbol: config.intersectSym })
          ]
          this.map.findLayerById('sLayer').addMany(arr)
          break
        case 'right':
          arr = [
            new Graphic({ geometry: differenceGeometryRight, symbol: config.intersectSym, attributes: attr2 }),
            osGraphics0
          ]
          this.map.findLayerById('sLayer').addMany(arr)
          break
        case 'all':
          let symmetricDifferenceGraphic = []
          for (let item of symmetricDifferenceGeometrys) {
            let graphic = new Graphic({ geometry: item, symbol: config.intersectSym, attributes: attr1 })
            symmetricDifferenceGraphic.push(graphic)
          }
          let graphic1 = new Graphic({ geometry: symmetricDifferenceGeometrys[0], symbol: config.intersectSym, attributes: attr1 })
          let graphic2 = new Graphic({ geometry: symmetricDifferenceGeometrys[1], symbol: config.intersectSym, attributes: attr2 })
          arr = [
            graphic1,
            new Graphic({ geometry: intersectGeometry, symbol: config.intersectSym }),
            graphic2
          ]
          this.map.findLayerById('sLayer').addMany(arr)
          break
        case 'contains':
          let contains = geometryEngine.contains(osGraphics0.geometry, osGraphics1.geometry)
          if (contains) {
            arr = [
              new Graphic({ geometry: differenceGeometryLeft, symbol: config.intersectSym, attributes: attr1 }),
              osGraphics1
            ]
          } else {
            arr = [
              new Graphic({ geometry: differenceGeometryRight, symbol: config.intersectSym, attributes: attr2 }),
              osGraphics0
            ]
          }
          this.map.findLayerById('sLayer').addMany(arr)
          break
        default:
          break
      }
    },
    /* 鼠标移出 */
    async intersectOut () {
      console.log(541564165)
      /* 清空选择层 */
      this.map.findLayerById('sLayer').removeAll()
      let arr = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        gra.symbol = config.selectionSym
        arr.push(gra)
      }
      if (this.intersectModal) {
        this.map.findLayerById('sLayer').addMany(arr)
      }
    },
    /* 属性弹框打开的时候，鼠标移入事件失效 */
    intersectAttrOver (type) {
      if (!this.intersectPanel) {
        this.intersectOver(type)
      }
    },
    /* 属性弹框打开的时候，鼠标移出事件失效 */
    intersectAttrOut (type) {
      if (!this.intersectPanel) {
        this.intersectOut(type)
      }
    },
    /* 打开属性控制面板 */
    openIntersectAttrPanel (type) {
      if (this.intersectPanelType !== type) {
        this.intersectPanel = true
        this.intersectPanelType = type
      } else {
        this.intersectPanel = false
        this.intersectPanelType = ''
      }
    },
    /* 鼠标点击选中块高亮，选择层样式高亮，显示属性详情 */
    intersectChickList (attr) {
      // 激活点选的几何属性
      this.intersectObj = attr
    }
  }
}
