import config from '@/setting/mapConfig'
/* 服务器配置 */
export default {
  // 权限配置  0.业务员、1.业务员二次修改、2.审核员审核、3.查看
  userType: 0,
  // 地图中心
  centerPoint: config.centerPoint,
  // 业务图层字段
  orchardFiled: [
    {
      name: 'ObjectID',
      alias: '编号',
      type: 'oid'
    },
    {
      name: 'name',
      alias: '活动名称',
      type: 'string'
    },
    // 活动类型： 采石场,工矿用地,能源设施,交通设施,旅游设施,养殖场,农业用地,居民点,其他人工设施,道路
    {
      name: 'type',
      alias: '活动类型',
      type: 'integer'
    },
    {
      name: 'xzqh',
      alias: '行政区划',
      type: 'string'
    },
    // 所属区域类型： 核心区,缓冲区,试验区
    {
      name: 'region',
      alias: '所属区域类型',
      type: 'string'
    },
    {
      name: 'position',
      alias: '位置',
      type: 'string'
    },
    {
      name: 'area',
      alias: '面积（公顷）',
      type: 'string'
    },
    {
      name: 'center',
      alias: '中心经纬度',
      type: 'string'
    }
  ],
  // 坐标系
  spatialReference: config.centerPoint.spatialReference,
  // 基年数据的样式
  backSym: {
    type: 'simple-fill', // 自动生成新的simpleFillSymbol
    color: [36, 0, 255, 0.3],
    outline: {
      // 自动生成新的SimpleLineSymbol
      color: [36, 0, 255],
      width: 2
    }
  },
  // 业务图层的样式
  orchardSym: {
    type: 'simple-fill', // 自动生成新的simpleFillSymbol
    color: [175, 255, 155, 0.3],
    outline: {
      // 自动生成新的SimpleLineSymbol
      color: [175, 255, 155],
      width: 2
    }
  },
  // 选中的样式
  selectionSym: {
    type: 'simple-fill',
    color: [255, 255, 255, 0.25],
    outline: {
      color: 'black',
      width: 0
    }
  },
  // 批注的样式
  examSym: {
    type: 'simple-marker',
    color: [255, 0, 0],
    size: '8px',
    outline: {
      color: [255, 0, 0, 0.5],
      width: 0
    }
  },
  // 批注确认的样式
  examChangeSym: {
    type: 'simple-marker',
    color: [0, 128, 0],
    size: '8px',
    outline: {
      color: [0, 128, 0, 0.5],
      width: 0
    }
  },
  // 批注高亮的样式
  examLightSym: {
    type: 'simple-marker',
    color: [255, 184, 0],
    size: '8px',
    outline: {
      color: [0, 128, 0, 0.5],
      width: 0
    }
  },
  // 鼠标移动时绘制的样式
  offsetSym: {
    type: 'simple-fill',
    color: [255, 255, 255, 0],
    outline: {
      color: '#960AC4',
      style: 'short-dash',
      width: 1
    }
  },
  // 鼠标移动时绘制的样式
  intersectSym: {
    type: 'simple-fill',
    color: [255, 255, 255, 0],
    outline: {
      color: '#960AC4',
      width: 1
    }
  },
  // 属性面板选中的样式
  attributeSym: {
    type: 'simple-fill',
    color: [255, 255, 255, 0.25],
    outline: {
      color: '#960AC4',
      width: 1
    }
  },
  // 裁剪线的样式
  cutterSym: {
    type: 'simple-line', // 自动生成新的SimpleLineSymbol()
    style: 'short-dash',
    color: 'white',
    width: 1
  },
  // 面报错的样式
  errorPloygonSym: {
    type: 'simple-fill', // 自动生成新的simpleFillSymbol
    color: [175, 255, 155, 0.3],
    outline: {
      // 自动生成新的SimpleLineSymbol
      color: [255, 0, 0],
      width: 2
    }
  },
  // 计算面积单位
  areaUnit: 'hectares',
  // 计算长度单位
  lengthUnit: '',
  // 计算用的geometryService的服务地址
  geometryServerUrl: config.geometryServerUrl
}
