import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 选择 */
export default {
  data () {
    return {
      referChoiceLayerId: 'backLayer', // 被选择图层的Id
      referChoiceStatus: false,
      referOriginalSelectedGraphics: [], // 选中的图层，用于其他工具使用
      referSelectedGraphic: {}, // 选中的图层
      referChoicePM: {}, // 鼠标移动事件
      referChoiceScores: [], // 绘制的矩形的数据数组
      referChoiceCutter: {}, // 绘制的矩形
      referKeyDown: {
        shift: false,
        alt: false
      }
    }
  },
  methods: {
    /* 切换按钮状态 */
    referChoiceStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools(['choice', 'attribute'])
      if (!this.referChoiceStatus) {
        this.referChoiceStatus = true
        this.referCursorType = 'choice'
        this.referAddChoice()
      } else {
        this.referChoiceStatus = false
        this.referDelChoice()
      }
    },
    /* 选择 */
    async referAddChoice () {
      let geometryEngine = await arcgisPackage.geometryEngine
      // 键盘键按下,修改光标样式
      this.referChoiceKeyDown = this.referview.on('key-down', (evt) => {
        if (evt.key === 'Shift') {
          this.referKeyDown.shift = true
        }
        if (evt.key === 'Alt') {
          this.referKeyDown.alt = true
        }
        if (this.referKeyDown.shift && !this.referKeyDown.alt) {
          this.referCursorType = 'choiceAdd'
        } else if (!this.referKeyDown.shift && this.referKeyDown.alt) {
          this.referCursorType = 'choiceReduce'
        } else {
          this.referCursorType = 'choice'
        }
      })
      // 键盘键释放，修改光标样式
      this.referChoiceKeyUp = this.referview.on('key-up', (evt) => {
        if (evt.key === 'Shift') {
          this.referKeyDown.shift = false
        }
        if (evt.key === 'Alt') {
          this.referKeyDown.alt = false
        }
        if (this.referKeyDown.shift && !this.referKeyDown.alt) {
          this.cursorType = 'choiceAdd'
        } else if (!this.referKeyDown.shift && this.referKeyDown.alt) {
          this.cursorType = 'choiceReduce'
        } else {
          this.cursorType = 'choice'
        }
      })
      // 定义地图的按下事件
      this.referChoiceDown = this.referview.on('pointer-down', (evt) => {
        // 禁止移动地图
        this.referChoiceDrag = this.referview.on('drag', (event) => {
          event.stopPropagation()
        })
        // 禁止按住shift后的鼠标拖拽
        this.referChoiceDragShift = this.referview.on('drag', ['Shift'], (event) => {
          event.stopPropagation()
        })
        // 绘制矩形
        this.referDrawRectangle(evt)
        // 定义地图的拖动事件
        this.referChoicePM = this.referview.on('pointer-move', (evt) => {
          this.referUpdataRectangle(evt)
        })
      })
      // 定义地图的弹起事件
      this.referChoiceUp = this.referview.on('pointer-up', (event) => {
        // 删除地图的拖动事件
        this.referChoicePM.remove()
        // 删除地图禁止移动
        this.referChoiceDrag.remove()
        // 删除地图静止shift+拖拽功能
        this.referChoiceDragShift.remove()
        // 获取选中的几何
        let gras = []
        this.refermap.findLayerById(this.referChoiceLayerId).queryFeatures().then((res) => {
          for (let item of res.features) {
            let intersect = geometryEngine.intersects(this.referChoiceCutter.geometry, item.geometry)
            if (intersect) {
              item.symbol = config.selectionSym
              gras.push(item)
            }
          }
          if (event.native.shiftKey && !event.native.altKey) {
            // 加选 Shift
            let arr = []
            for (let item1 of gras) {
              let gra = true
              for (let item2 of this.referOriginalSelectedGraphics) {
                let equals = geometryEngine.equals(item1.geometry, item2.geometry)
                if (equals) {
                  gra = false
                }
              }
              if (gra) {
                item1.symbol = config.selectionSym
                arr.push(item1)
              }
            }
            gras = this.referOriginalSelectedGraphics.concat(arr)
          } else if (!event.native.shiftKey && event.native.altKey) {
            // 减选 Alt
            let arr = []
            for (let item1 of this.referOriginalSelectedGraphics) {
              let gra = true
              for (let item2 of gras) {
                let equals = geometryEngine.equals(item1.geometry, item2.geometry)
                if (equals) {
                  gra = false
                }
              }
              if (gra) {
                arr.push(item1)
              }
            }
            gras = arr
          }
          this.referOriginalSelectedGraphics = gras
          this.refermap.findLayerById('sLayer').removeAll()
          this.refermap.findLayerById('sLayer').addMany(gras)
          // 删除矩形
          this.referview.graphics.remove(this.referChoiceCutter)
        })
      })
    },
    /* 删除选择工具 */
    referDelChoice () {
      /* 删除鼠标事件 */
      let removes = (rem) => {
        if (rem) {
          rem.remove()
        }
      }
      removes(this.referChoiceDown)
      removes(this.referChoiceUp)
      removes(this.referChoiceKeyDown)
      removes(this.referChoiceKeyUp)
    },
    /* 在某个点绘制矩形，长宽等于0 */
    async referDrawRectangle (evt) {
      let Graphic = await arcgisPackage.Graphic
      let point = this.referview.toMap({ x: evt.x, y: evt.y })
      this.referChoiceScores = [[point.x, point.y], [point.x, point.y]]
      let vertices = this.referChoiceScores
      let rings = [vertices[0], [vertices[0][0], vertices[1][1]], vertices[1], [vertices[1][0], vertices[0][1]]]
      let lineGeom = {
        type: 'polygon',
        rings: rings,
        spatialReference: config.spatialReference
      }
      this.referChoiceCutter = new Graphic({
        geometry: lineGeom,
        symbol: config.cutterSym
      })
      this.referview.graphics.add(this.referChoiceCutter)
    },
    /* 更新矩形 */
    referUpdataRectangle (evt) {
      let point = this.referview.toMap({ x: evt.x, y: evt.y })
      // 将缓存数组最后一个坐标修改为鼠标当前位置
      this.referChoiceScores[this.referChoiceScores.length - 1] = [point.x, point.y]
      let vertices = this.referChoiceScores
      let rings = [vertices[0], [vertices[0][0], vertices[1][1]], vertices[1], [vertices[1][0], vertices[0][1]]]
      // 深度复制cutter到新对象
      let cutterTmp = this.referChoiceCutter.clone()
      // 修改坐标位置
      cutterTmp.geometry.rings = rings
      // 删除原始矩形
      this.referview.graphics.remove(this.referChoiceCutter)
      // 添加新的矩形
      this.referview.graphics.add(cutterTmp)
      this.referChoiceCutter = cutterTmp
    }
  }
}
