import arcgisPackage from '@/utils/arcgisTools'
import config from '../config'
import '../font/iconfont.css'
import choice from './choice'
import cut from './cut'
import merge from './merge'
import add from './add'
import scatter from './scatter'
import intersect from './intersect'
import del from './delete'
import attribute from './attribute'
import close from './close'
import load from './load'
import copy from './copy'
import exam from './exam'
import icoChange from './icoChange'
import addLayer from '../method/addLayer'
import { cacuLonLat } from '../common'
import referChoice from './referChoice'
/* 新建 */
export default {
  data () {
    return {
    }
  },
  mixins: [choice, referChoice, cut, merge, add, scatter, del, intersect, attribute, close, load, copy, exam, icoChange, addLayer],
  methods: {
    /* 添加自定义工具 */
    async addTools () {
      // 添加工具栏
      this.view.ui.add(document.getElementById('toolbar'), 'top-right')
      // 添加基年数据工具栏
      this.referview.ui.add(document.getElementById('orcharToolbar'), 'top-right')
      // 添加融合属性选择面板
      this.view.ui.add(document.getElementById('mergeModal'), 'top-right')
      // 添加交集面板
      this.view.ui.add(document.getElementById('intersectModal'), 'top-right')
      // 添加交集属性面板
      this.view.ui.add(document.getElementById('intersectPanel'), 'top-right')
      // 添加属性面板
      this.view.ui.add(document.getElementById('attributePanel'), 'top-right')
      // 添加批注面板
      this.view.ui.add(document.getElementById('annoPanel'), 'top-right')
    },
    /* 关闭工具条其他工具 */
    closeOtherTools (toolNames) {
      this.cursorType = 'default'
      if (!toolNames) {
        toolNames = ''
      }
      /* 关闭选择工具 */
      if (toolNames.indexOf('choice') === -1) {
        if (this.choiceStatus) {
          this.choiceStatus = false
          this.delChoice()
        }
      }
      /* 关闭剪切工具 */
      if (toolNames.indexOf('cut') === -1) {
        if (this.cutstatus) {
          this.cutstatus = false
          this.delCut()
        }
      }
      /* 关闭添加工具 */
      if (toolNames.indexOf('add') === -1) {
        if (this.addStatus) {
          this.delSketch()
        }
      }
      /* 关闭融合工具 */
      if (toolNames.indexOf('merge') === -1) {
        if (this.mergeStatus) {
          this.mergeStatus = false
          this.delMerge()
        }
      }
      /* 关闭交集工具 */
      if (toolNames.indexOf('intersect') === -1) {
        if (this.intersectStatus) {
          this.intersectStatus = false
          this.delIntersect()
        }
      }
      /* 关闭属性操作工具 */
      if (toolNames.indexOf('attribute') === -1) {
        if (this.attributeStatus) {
          this.attributeStatus = false
        }
      }
      /* 关闭审核面板 */
      if (toolNames.indexOf('anno') === -1) {
        if (this.annoStatus) {
          this.annoStatus = false
        }
      }
    },
    /* 激活选择工具 */
    activeChoice () {
      this.choiceStatus = true
      this.cursorType = 'choice'
      this.addChoice()
    },
    /* 为feature获取面积和中心坐标 */
    async getAreaCenter (val) {
      let geometryEngine = await arcgisPackage.geometryEngine
      let GeometryService = await arcgisPackage.GeometryService
      let AreasAndLengthsParameters = await arcgisPackage.AreasAndLengthsParameters
      if (this.view.spatialReference.isWGS84) {
        val.attributes.area = geometryEngine.geodesicArea(val.geometry, 'hectares').toFixed(2)
        val.attributes.center = cacuLonLat(val.geometry.extent.center.latitude) + ',' + cacuLonLat(val.geometry.extent.center.longitude)
      } else {
        let geometryService = new GeometryService({
          url: config.geometryServerUrl
        })
        let areasAndLengths = await geometryService.areasAndLengths(new AreasAndLengthsParameters({
          calculationType: 'geodesic',
          areaUnit: 'hectares',
          lengthUnit: 'kilometers',
          polygons: [ val.geometry ]
        }))
        val.attributes.area = areasAndLengths.areas[0].toFixed(2)
        val.attributes.center = cacuLonLat(val.geometry.extent.center.x) + ',' + cacuLonLat(val.geometry.extent.center.y)
        if (val.attributes.name === null) {
          val.attributes.name = '未知活动'
        }
      }
    },
    /* 判断所有面中是否存在相交 */
    async judgeIntersect (arr) {
      console.log(arr.length)
      let geometryEngine = await arcgisPackage.geometryEngine
      // 判断是否存在相交面，并获取相交面和不相交面
      let intersectArr = []
      let unIntersectArr = []
      for (let item1 of arr) {
        let onOff = false
        for (let item2 of arr) {
          if (item1.uid !== item2.uid && !onOff) {
            if (geometryEngine.intersects(item1.geometry, item2.geometry)) {
              let obj = item1.clone()
              obj.symbol = config.errorPloygonSym
              intersectArr.push(obj)
              onOff = true
            }
          }
        }
        if (!onOff) {
          let obj = item1.clone()
          obj.symbol = config.orchardSym
          unIntersectArr.push(obj)
        }
      }
      // 将修改样式后的相交不相交面放入图层
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany([...intersectArr, ...unIntersectArr])
      // 若存在相交面返回false，不存在返回true
      if (intersectArr.length > 0) {
        return false
      } else {
        return true
      }
    }
  }
}
