  },
  mounted () {
    this.createMap()
  },
  computed: {
    compareTableData () {
      let tempSyhy = this.tableData[0].syhy - this.tableData[1].syhy
      let tempSwdyx = this.tableData[0].swdyx - this.tableData[1].swdyx
      let tempStbc = this.tableData[0].stbc - this.tableData[1].stbc
      let tempFfgs = this.tableData[0].ffgs - this.tableData[1].ffgs
      let tempQtstgn = this.tableData[0].qtstgn - this.tableData[1].qtstgn
      let tempStls = this.tableData[0].stls - this.tableData[1].stls
      let tempTdsh = this.tableData[0].tdsh - this.tableData[1].tdsh
      let tempSmh = this.tableData[0].smh - this.tableData[1].smh
      let tempQtmgx = this.tableData[0].qtmgx - this.tableData[1].qtmgx

      let temp = {
        year: '变化量',
        syhy: tempSyhy,
        swdyx: tempSwdyx,
        stbc: tempStbc,
        ffgs: tempFfgs,
        qtstgn: tempQtstgn,
        stls: tempStls,
        tdsh: tempTdsh,
        smh: tempSmh,
        qtmgx: tempQtmgx
      }
      let arr = JSON.parse(JSON.stringify(this.tableData))
      arr[0].year = this.periods1 === '' ? '' : ('第' + this.periods1 + '版本')
      arr[1].year = this.periods2 === '' ? '' : ('第' + this.periods2 + '版本')
      console.log([
        ...arr,
        temp
      ])
      return [
        ...arr,
        temp
      ]
    }
  },
  methods: {
    // 生成地图的函数
    async createMap () {
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })

      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.map2 = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'map1',
        center: config.centerPoint,
        zoom: 6
      })
      this.view2 = new MapView({
        map: this.map2,
        container: 'map2',
        center: config.centerPoint,
        zoom: 6
      })
      let views = []
      views.push(this.view)
      views.push(this.view2)
      // 设置鼠标滚轮事件
      this.view.on('mouse-wheel', e => {
        // 延迟获取范围的函数
        window.setTimeout(() => {
          // 获取操作图层的范围
          let fullextent = this.view.extent
          // 遍历所有视图来设置这个范围
          views &&
          views.forEach(nview => {
            nview.extent = fullextent
          })
        }, 1000)
      })
      // 设置鼠标拖拽事件
      this.view2.on('drag', e => {
        // 获取操作图层的范围
        let fullextent = this.view.extent
        // 遍历所有视图来设置这个范围
        views.forEach(nview => {
          nview.extent = fullextent
        })
      })
    },
    /* 获取红线版本列表 */
    getVersionList (e) {
      listShp({
        data: {
          // id: e
        }
      }).then((res) => {
        res.data.data.forEach(e => {
          e.version = '第 ' + e.version + ' 版本'
        })
        this.periods = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 红线版本下拉的change事件（左侧获取该option的id）
    redLineChange1 (id) {
      getShpDetailById({
        data: {
          id
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.addFeatureLayer(res.data.data[0].service_url, this.map, this.view)
          for (let i = 0; i < 3; i++) {
            this.$set(this.tableData, 0, {
              year: 3,
              syhy: 0,
              swdyx: 0,
              stbc: 0,
              ffgs: 0,
              qtstgn: 0,
              stls: 0,
              tdsh: 0,
              smh: 0,
              qtmgx: 0
            })
          }
          this.periods.forEach(val => {
            if (val.id === id) {
              this.tableData[0].year = val.name
            }
          })
          res.data.data.forEach(val => {
            switch (val.dtype) {
              case '0101':
                this.tableData[0].syhy = val.area
                break
              case '0102':
                this.tableData[0].swdyx = val.area
                break
              case '0102':
                this.tableData[0].stbc = val.area
                break
              case '0103':
                this.tableData[0].ffgs = val.area
                break
              case '0104':
                this.tableData[0].qtstgn = val.area
                break
              case '0201':
                this.tableData[0].stls = val.area
                break
              case '0202':
                this.tableData[0].tdsh = val.area
                break
              case '0203':
                this.tableData[0].smh = val.area
                break
              case '0204':
                this.tableData[0].qtmgx = val.area
                break
              default:
                break
            }
          })
        } else {
          this.$Notice.error({ title: '获取红线数据失败' })
        }
      })
    },
    // 红线版本下拉的change事件（右侧获取该option的id）
    redLineChange2 (id) {
      getShpDetailById({
        data: {
          id
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.addFeatureLayer(res.data.data[0].service_url, this.map2, this.view2)
          for (let i = 0; i < 3; i++) {
            this.$set(this.tableData, 1, {
              year: 3,
              syhy: 0,
              swdyx: 0,
              stbc: 0,
              ffgs: 0,
              qtstgn: 0,
              stls: 0,
              tdsh: 0,
              smh: 0,
              qtmgx: 0
            })
          }
          this.periods.forEach(val => {
            if (val.id === id) {
              this.tableData[1].year = val.name
            }
          })
          res.data.data.forEach(val => {
            switch (val.dtype) {
              case '0101':
                this.tableData[1].syhy = val.area
                break
              case '0102':
                this.tableData[1].swdyx = val.area
                break
              case '0102':
                this.tableData[1].stbc = val.area
                break
              case '0103':
                this.tableData[1].ffgs = val.area
                break
              case '0104':
                this.tableData[1].qtstgn = val.area
                break
              case '0201':
                this.tableData[1].stls = val.area
                break
              case '0202':
                this.tableData[1].tdsh = val.area
                break
              case '0203':
                this.tableData[1].smh = val.area
                break
              case '0204':
                this.tableData[1].qtmgx = val.area
                break
              default:
                break
            }
          })
        } else {
          this.$Notice.error({ title: '获取红线数据失败' })
        }
      })
    },
    async addFeatureLayer (url, map, view) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let renderer = {
        type: 'simple',
        symbol: {
          type: 'simple-fill',
          color: [ 255, 0, 0, 0 ],
          outline: {
            width: 2,
            color: 'red'
          }
        }
      }
      // 图层数据查看
      let featureLayer = new FeatureLayer({
        url: url,
        renderer: renderer
      })
      if (map) {
        map.add(featureLayer)
        featureLayer.on('layerview-create', (event) => {
          view.extent = featureLayer.fullExtent.expand(1.4) // 定位图层
        })
      } else {
        this.map.add(featureLayer)
        featureLayer.on('layerview-create', (event) => {
          this.view.extent = featureLayer.fullExtent.expand(1.4) // 定位图层
        })
      }
    },
    // 添加红线审核地图数据
    async addLayer (url, map, view) {
      let MapImageLayer = await arcgisPackage.MapImageLayer
      let mapimagelayer = new MapImageLayer({
        url: url
      })
      if (map) {
        map.add(mapimagelayer)
        mapimagelayer.on('layerview-create', (event) => {
          view.extent = mapimagelayer.fullExtent.expand(1.4) // 定位图层
        })
      } else {
        this.map.add(mapimagelayer)
        mapimagelayer.on('layerview-create', (event) => {
          this.view.extent = mapimagelayer.fullExtent.expand(1.4) // 定位图层
        })
      }
    }
  }
}

</script>

<style lang="scss" scoped>
  .contrast {
    width: 100%;
    height: 100%;
    overflow: hidden;
    .contrast-body {
      width: 100%;
      height: calc(100% - 210px);
      overflow: hidden;
      .ivu-col-span-12 {
        height: 100%;
        width: 49.6% !important;
