import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 新建 */
export default {
  data () {
    return {
      annoStatus: false, //
      pointData: [], // 批注图层数据
      changeData: [], // 批注确认图层数据
      annoActive: {
        attributes: {}
      }, // 当前激活的批注点
      annoCon: '', // 当前显示的批注点批注内容（input文本域显示）
      listInputStatus: false // 列表中的input框的显示隐藏
    }
  },
  methods: {
    /* 审核面板的显示隐藏 */
    annoStatusToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('anno')
      if (!this.annoStatus) {
        this.annoStatus = true
      } else {
        this.annoStatus = false
      }
    },
    /* 获取列表数据 */
    getAnnoListData () {
      this.map.findLayerById('examLayer').queryFeatures().then((res) => {
        this.pointData = res.features
      })
      this.map.findLayerById('examChangeLayer').queryFeatures().then((res) => {
        this.changeData = res.features
      })
    },
    /* 点击添加 */
    async addCnno () {
      this.cursorType = 'anno'
      let Graphic = await arcgisPackage.Graphic
      this.annotatClick = this.view.on('click', (evt) => {
        this.cursorType = 'default'
        let point = this.view.toMap({ x: evt.x, y: evt.y })
        this.annotatCoordinate = [point.x, point.y]
        this.annotatPanelStatus = true
        /* 创建批注点 */
        let geometry = {
          type: 'point',
          x: this.annotatCoordinate[0],
          y: this.annotatCoordinate[1],
          spatialReference: config.spatialReference
        }
        let graphic = new Graphic({ geometry: geometry, symbol: config.examSym, attributes: {} })
        console.log(graphic)
        this.annotatPoint = graphic
        this.map.findLayerById('examLayer').applyEdits({
          addFeatures: [graphic]
        })
        // 将点同步到面板显示列表
        this.pointData.push(graphic)
        // 关闭地图点击添加事件
        let removes = (rem) => {
          if (rem) {
            rem.remove()
            rem = undefined
          }
        }
        removes(this.annotatClick)
      })
    },
    /* 点击删除 */
    annoDelete (item) {
      this.map.findLayerById('examLayer').applyEdits({
        deleteFeatures: [item]
      })
      this.map.findLayerById('examLayer').queryFeatures().then((res) => {
        this.pointData = res.features
      })
    },
    /* 点击选中 */
    pointAnnoLi (item, type) {
      // 选中li高亮
      this.annoActive = item.clone()
      this.annoActive.attributes.type = type
      // 将地图移动到选择点位置
      let x = item.geometry.x / 20037508.34 * 180
      let y = item.geometry.y / 20037508.34 * 180
      y = 180 / Math.PI * (2 * Math.atan(Math.exp(y * Math.PI / 180)) - Math.PI / 2)
      this.view.center = [x, y]
      // 清空高量层
      this.map.findLayerById('examLightLayer').queryFeatures().then((res) => {
        // 删除数据图层所有的要素
        this.map.findLayerById('examLightLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
      // 在高亮层新建选中点
      this.map.findLayerById('examLightLayer').applyEdits({
        addFeatures: [item.clone()]
      })
    },
    /* 点击列表中编辑按钮 */
    editAnnotatLi (item) {
      if (!this.listInputStatus) {
        // 选中批注详情显示
        this.annoCon = item.attributes.annotat
        this.listInputStatus = true
      } else {
        this.editAnno()
        // 选中批注详情显示
        this.annoCon = ''
        this.listInputStatus = false
      }
    },
    /* 保存批注 */
    async editAnno () {
      this.annoActive.attributes.annotat = this.annoCon
      if (this.annoActive.attributes.type === 'point') {
        // 将修改后的批注添加进地图
        this.map.findLayerById('examLayer').applyEdits({
          updateFeatures: [this.annoActive]
        })
        // 刷新属性列表
        await this.map.findLayerById('examLayer').queryFeatures().then((res) => {
          this.pointData = res.features
        })
      } else {
        // 将修改后的批注添加进地图
        this.map.findLayerById('examChangeLayer').applyEdits({
          updateFeatures: [this.annoActive]
        })
        // 刷新属性列表
        await this.map.findLayerById('examChangeLayer').queryFeatures().then((res) => {
          this.changeData = res.features
        })
      }
    },
    /* 点击修改批注完成状态 */
    async editAnnoStatus (item) {
      // 将点从批注层移入批注确定层
      this.map.findLayerById('examLayer').applyEdits({
        deleteFeatures: [item.clone()]
      })
      this.map.findLayerById('examChangeLayer').applyEdits({
        addFeatures: [item.clone()]
      })
      // 刷新属性列表
      await this.map.findLayerById('examLayer').queryFeatures().then((res) => {
        this.pointData = res.features
      })
      await this.map.findLayerById('examChangeLayer').queryFeatures().then((res) => {
        this.changeData = res.features
      })
    }
  }
}
