import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 新建 */
export default {
  data () {
    return {
    }
  },
  methods: {
    /* 添加Feature图层 */
    async addFeatureLayer (map) {
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let Graphic = await arcgisPackage.Graphic
      let Field = await arcgisPackage.Field
      // 样式渲染器，添加到FeatureLayer时候用的
      let orchardRenderer = new SimpleRenderer({ symbol: config.orchardSym })
      // 通过自定义坐标，生成几何图形，创建FeatureLayer
      let graphic = new Graphic({
        geometry: {
          type: 'polygon',
          rings: [[
            [0.1, 0.1],
            [0.11, 0.12],
            [0.11, 0.13],
            [0.1, 0.1]
          ]],
          spatialReference: config.spatialReference
        },
        symbol: config.cutterSym
      })
      let features = [graphic]
      // 从config文件中获取图层字段
      let fields = []
      for (let item of config.orchardFiled) {
        let field = new Field(item)
        fields.push(field)
      }
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'orchardLayer',
        id: 'orchardLayer',
        title: '业务数据',
        objectIdField: 'ObjectID',
        source: features,
        renderer: orchardRenderer,
        fields: fields
      })
      // 将数据图层添加到地图中
      map.addMany([orchardLayer])
      // 删除创建featureLayer时添加的默认要素
      await map.findLayerById('orchardLayer').queryFeatures().then((res) => {
        map.findLayerById('orchardLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
    },
    /* 添加选择图层 */
    async addSelectionLayer (map) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      // 创建选择层以显示选择符号
      let sLayer = new GraphicsLayer({
        layerId: 'sLayer',
        title: '选择层',
        id: 'sLayer'
      })
      // 将数据图层与选择图层一次性添加到地图中
      map.addMany([ sLayer ])
    },
    /* 添加批注点高量图层 */
    async addExamLigetLayer (map) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let Field = await arcgisPackage.Field
      // 样式渲染器，添加到FeatureLayer时候用的
      let orchardRenderer = new SimpleRenderer({ symbol: config.examLightSym })
      // 通过自定义坐标，生成几何图形，创建FeatureLayer
      let features = [
        {
          attributes: {
            ObjectID: 1,
            annotat: '范围不正确'
          },
          geometry: {
            type: 'point',
            x: 12936456.29736119,
            y: 4887448.934942529,
            spatialReference: {
              'wkid': 102100,
              'latestWkid': 3857
            }
          }
        }
      ]
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'examLightLayer',
        id: 'examLightLayer',
        title: '批注确认图层',
        objectIdField: 'ObjectID',
        source: features,
        renderer: orchardRenderer,
        fields: [new Field({
          name: 'ObjectID',
          alias: 'ObjectID',
          type: 'oid'
        }), new Field({
          name: 'annotat',
          alias: '批注',
          type: 'string'
        })]
      })
      // 将数据图层添加到地图中
      map.addMany([orchardLayer])
      // 删除创建featureLayer时添加的默认要素
      await map.findLayerById('examLightLayer').queryFeatures().then((res) => {
        map.findLayerById('examLightLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
    },
    /* 添加批注图层 */
    async addExamLayer (map) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let Field = await arcgisPackage.Field
      const labelClass = {
        symbol: {
          type: 'text',
          color: 'red',
          haloColor: 'black',
          font: {
            size: 12,
            weight: 'bold'
          }
        },
        labelPlacement: 'above-center',
        labelExpressionInfo: {
          expression: '$feature.annotat'
        }
      }
      // 样式渲染器，添加到FeatureLayer时候用的
      let orchardRenderer = new SimpleRenderer({ symbol: config.examSym })
      // 通过自定义坐标，生成几何图形，创建FeatureLayer
      let features = [
        {
          geometry: {
            type: 'point',
            x: 12936456.29736119,
            y: 4887448.934942529,
            spatialReference: {
              'wkid': 102100,
              'latestWkid': 3857
            }
          }
        }
      ]
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'examLayer',
        id: 'examLayer',
        title: '批注图层',
        objectIdField: 'ObjectID',
        source: features,
        renderer: orchardRenderer,
        fields: [new Field({
          name: 'ObjectID',
          alias: 'ObjectID',
          type: 'oid'
        }), new Field({
          name: 'annotat',
          alias: '批注',
          type: 'string'
        })],
        labelingInfo: [labelClass],
        labelsVisible: true
      })
      // 将数据图层添加到地图中
      map.addMany([orchardLayer])
      // 删除创建featureLayer时添加的默认要素
      await map.findLayerById('examLayer').queryFeatures().then((res) => {
        map.findLayerById('examLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
    },
    /* 添加批注确定图层 */
    async addExamChangeLayer (map) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let Field = await arcgisPackage.Field
      const labelClass = {
        symbol: {
          type: 'text',
          color: 'green',
          haloColor: 'black',
          font: {
            size: 12,
            weight: 'bold'
          }
        },
        labelPlacement: 'above-center',
        labelExpressionInfo: {
          expression: '$feature.annotat'
        }
      }
      // 样式渲染器，添加到FeatureLayer时候用的
      let orchardRenderer = new SimpleRenderer({ symbol: config.examChangeSym })
      // 通过自定义坐标，生成几何图形，创建FeatureLayer
      let features = [
        {
          geometry: {
            type: 'point',
            x: 12936456.29736119,
            y: 4887448.934942529,
            spatialReference: {
              'wkid': 102100,
              'latestWkid': 3857
            }
          }
        }
      ]
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'examChangeLayer',
        id: 'examChangeLayer',
        title: '批注确认图层',
        objectIdField: 'ObjectID',
        source: features,
        renderer: orchardRenderer,
        fields: [new Field({
          name: 'ObjectID',
          alias: 'ObjectID',
          type: 'oid'
        }), new Field({
          name: 'annotat',
          alias: '批注',
          type: 'string'
        })],
        labelingInfo: [labelClass],
        labelsVisible: true
      })
      // 将数据图层添加到地图中
      map.addMany([orchardLayer])
      // 删除创建featureLayer时添加的默认要素
      await map.findLayerById('examChangeLayer').queryFeatures().then((res) => {
        map.findLayerById('examChangeLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
    },
    /* 添加基年图层 */
    async addBackLayer (map) {
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let Graphic = await arcgisPackage.Graphic
      let Field = await arcgisPackage.Field
      // 样式渲染器，添加到FeatureLayer时候用的
      let orchardRenderer = new SimpleRenderer({ symbol: config.backSym })
      // 通过自定义坐标，生成几何图形，创建FeatureLayer
      let graphic = new Graphic({
        geometry: {
          type: 'polygon',
          rings: [[
            [0.1, 0.1],
            [0.11, 0.12],
            [0.11, 0.13],
            [0.1, 0.1]
          ]],
          spatialReference: config.spatialReference
        },
        symbol: config.cutterSym
      })
      let features = [graphic]
      // 从config文件中获取图层字段
      let fields = []
      for (let item of config.orchardFiled) {
        let field = new Field(item)
        fields.push(field)
      }
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'backLayer',
        id: 'backLayer',
        title: '基年数据',
        objectIdField: 'ObjectID',
        source: features,
        renderer: orchardRenderer,
        fields: fields
      })
      // 将数据图层添加到地图中
      map.addMany([orchardLayer])
      // 删除创建featureLayer时添加的默认要素
      map.findLayerById('backLayer').queryFeatures().then((res) => {
        map.findLayerById('backLayer').applyEdits({
          deleteFeatures: res.features
        })
      })
    },
    /* 添加tools图层 */
    async addAllToolLayers (map) {
      /* 添加基年数据图层 */
      await this.addBackLayer(map)
      /* 添加Feature图层 */
      await this.addFeatureLayer(map)
      /* 添加选择图层 */
      await this.addSelectionLayer(map)
      /* 添加批注确认图层 */
      await this.addExamChangeLayer(map)
      /* 添加批注图层 */
      await this.addExamLayer(map)
      /* 添加批注高亮图层 */
      await this.addExamLigetLayer(map)
    },
    /* 添加tools图层 */
    async addReferToolLayers (map) {
      /* 添加基年数据图层 */
      await this.addBackLayer(map)
      /* 添加选择图层 */
      await this.addSelectionLayer(map)
    }
  }
}
