import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 新建 */
export default {
  data () {
    return {
      addStatus: false
    }
  },
  methods: {
    /* 切换合并按钮状态 */
    async addStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('add')
      if (!this.addStatus) {
        this.map.findLayerById('sLayer').removeAll()
        this.map.findLayerById('orchardLayer').queryFeatures().then((res) => {
          // 删除数据图层所有的要素
          this.map.findLayerById('orchardLayer').applyEdits({
            deleteFeatures: res.features
          })
          // 将所有要素添加在选择图层中
          let arr = []
          for (let item of res.features) {
            let gra = item.clone()
            gra.symbol = config.orchardSym
            arr.push(gra)
          }
          this.map.findLayerById('sLayer').addMany(arr)
        })
        this.addStatus = true
        this.addSketch()
      } else {
        this.delSketch()
      }
    },
    /* 添加编辑工具 */
    async addSketch () {
      let Sketch = await arcgisPackage.Sketch
      this.sketch = new Sketch({
        layer: this.map.findLayerById('sLayer'),
        view: this.view
      })
      this.view.ui.add(this.sketch, 'top-right')
      this.sketch.on('update', (event) => {
        if (event.state === 'complete') {
          this.map.findLayerById('sLayer').remove(event.graphic)
        }
      })
    },
    /* 销毁组件 */
    async delSketch () {
      // 获取新增片块的中心点和面积
      let arr = this.map.findLayerById('sLayer').graphics.items
      console.log(arr)
      for (let i in arr) {
        if (arr[i].attributes === undefined) {
          arr[i].attributes = []
          for (let item of config.orchardFiled) {
            arr[i].attributes[item.name] = null
          }
        }
        await this.getAreaCenter(arr[i])
      }
      this.addStatus = false
      this.map.findLayerById('orchardLayer').applyEdits({
        addFeatures: arr
      })
      this.map.findLayerById('sLayer').removeAll()
      this.sketch.destroy()
      if (!this.sketch) {
        this.view.ui.remove(this.sketch)
        this.sketch = undefined
      }
    }
  }
}
