<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 0px)"></QueryBox>
      <div class="queryBtns">
<!--
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.adjustmentAdd.mobileUserBuild = true">新建</Button>
-->
      </div>
    </div>
    <div class="content_box">
      <div style="height: calc(100% - 15px);">
        <Table :loading="loading" border stripe :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <div class="add_box" :class="{addActive:addActive}">
      <adjustmentAdd ref="adjustmentAdd" @getNewList="getNewList"></adjustmentAdd>
    </div>
    <!-- 查看地图-->
    <div class="map_box" :class="{MapActive:MapActive}">
      <!--<MapView ref="MapView" @MapSignOut="MapSignOut" :mapId="mapId"></MapView>-->
      <adjustmentMap ref="activitiesInspectTableMap" @signOut="signOuta"></adjustmentMap>
    </div>
    <!-- 结果对比-->
    <div class="Comparison_box" :class="{ComparisonActive:ComparisonActive}">
      <imageComparison ref="imageComparison" @ComparisonSignOut="ComparisonSignOut" :imageId="imageId"></imageComparison>
    </div>
    <!--拒绝审核理由弹框 -->
    <Modal
      v-model="auditModal"
      @on-cancel="handleCancel">
      <div>
        <Form :model="auditData" ref="statusForm" :label-width="90" style="padding:0 50px;margin-top: 40px" :rules="DataRule" >
          <FormItem prop="ck070" label="审核意见：">
            <Input type="textarea" v-model.trim="auditData.ck070" placeholder="请输入审核意见" size="default"/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" @click="handleAudit" style="margin-right: 10px">审核通过</Button>
        <Button @click="cancelAudit">审核拒绝</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import QueryBox from '@/components/publicQuery'
import adjustmentAdd from './adjustment_add'
import MapView from './MapView'
import imageComparison from './imageComparison'
import adjustmentMap from './adjustment_map'
// 查询列表
import { getList, pointStageExamine, deleteShp } from '@/api/adjustment'
import { renderTime } from '@/utils/common'
export default {
  name: 'adjustment',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: window.innerHeight  - 320,
      /* 表头 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          align: 'center',
          width: 80,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '版本批次',
          key: 'id',
          align: 'center',
          width: 170,
          ellipsis: true,
          tooltip: true,
          render: (h, params) => {
            return h('span', '第' + params.row.id + '版本')
          }
        },
        {
          title: '数据地址',
          key: 'serviceUrl',
          align: 'center',
          minWidth: 200,
          ellipsis: true,
          tooltip: true
        },
        {
          title: '更新时间',
          key: 'createDate',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h('span', renderTime(params.row.createDate))
          }
        },
        {
          title: '操作',
          key: 'isBs',
          align: 'center',
          width: 150,
          render: (h, params) => {
            return h('div', [
              /* h('Button', {
                props: {
                  type: 'default',
                  size: 'small',
                  icon: 'ios-funnel'
                },
                style: {
                  marginRight: '10px'
                },
                on: {
                  click: () => {
                    this.auditModal = true
                  }
                }
              }, '审核'),
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small',
                  icon: 'ios-map'
                },
                style: {
                  marginRight: '10px',
                  background: 'rgb(16,186,204)',
                  borderColor: 'rgb(16,186,204)'
                },
                on: {
                  click: () => {
                    this.mapId = params.row.id
                    this.mapUrlData(params.row.serviceUrl)
                    console.log(params.row.serviceUrl)
                    this.$refs.activitiesInspectTableMap.addFeatureLayer(params.row.serviceUrl)
                  }
                }
              }, '地图'), */
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'ios-browsers'
                },
                style: {
                  marginRight: '10px'
                },
                on: {
                  click: () => {
                    this.$refs.imageComparison.getVersionList(params.row.id)
                    this.$refs.imageComparison.periods1 = params.row.id
                    this.$refs.imageComparison.redLineChange1(params.row.id)
                    this.ComparisonActive = true
                  }
                }
              }, '版本对比'),
              /* h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该数据么？',
                      onOk: () => {
                        this.infoDel(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除') */
            ])
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 总条数 */
      total: 0,
      /* 初始化页码 */
      pageNum: 1,
      /* 页容量自适应 */
      pageSize: Math.floor((window.innerHeight  - 320) / 48),
      /* 加载 */
      loading: false,
      /* 新建框状态 */
      addActive: false,
      /* 查看数据的id */
      seeId: 0,
      fileUrl: '',
      // 结果对比页面显示状态
      ComparisonActive: false,
      imageId: 0,
      // 地图页面显示状态
      MapActive: false,
      mapId: 0,
      // 审核意见
      auditModal: false,
      auditData: {
        ck070: ''
      },
      // 审核意见校验
      DataRule: {
        ck070: [
          { required: true, message: '审核意见不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    QueryBox,
    adjustmentAdd,
    MapView,
    imageComparison,
    adjustmentMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '数据名称'
        }
      ]
    }
  },
  watch: {
    queryData () {
      this.getTableData()
    }
  },
  created () {
    this.getTableData()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 320
      this.pageSize = Math.floor((window.innerHeight - 320) / 48)
      this.getTableData()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.getTableData()
    },
    signOuta () {
      this.MapActive = false
    },
    /* 获取项目列表 */
    getTableData () {
      this.loading = true
      getList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total = res.data.data.total
        this.loading = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableData()
    },
    /* 删除 */
    infoDel (e) {
      deleteShp({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          // 避免刪除当前页最后一条数据时显示--无数据
          if (this.data1.length === 1 && this.pageNum > 1) {
            this.pageNum = this.pageNum - 1
          }
          this.getTableData()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 新建页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 新建保存后刷新列表 */
    getNewList () {
      this.getTableData()
      this.pageNum = 1
    },
    /* 结果对比页面退出 */
    ComparisonSignOut (e) {
      this.ComparisonActive = e
    },
    /* 查看地图页面退出 */
    MapSignOut (e) {
      this.MapActive = e
    },
    /* 地图页面带参数url */
    /* mapUrlData (url) {
      // 展示地图页面
      this.MapActive = true
      // 获取url
      console.log(url)
      let mapimagelayer = new arcgisPackage.MapImageLayer({
        url: url
      })

      this.map.add(mapimagelayer)
      mapimagelayer.on('layerview-create', (event) => {
        this.view.extent = mapimagelayer.fullExtent.expand(1.4) // 定位图层
      })
    }, */
    mapUrlData (url) {
      // 展示地图页面
      this.MapActive = true

    },
    // 审核通过
    handleAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '1',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核通过' })
              this.handleCancel()
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
    handleCancel () {
    },
    // 审核拒绝
    cancelAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '2',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核拒绝' })
              this.handleCancel()
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      height: calc(100% - 65px);
      overflow: hidden;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .add_box, .Comparison_box, .map_box{
      height: calc(100% - 30px);
      width: calc(100% - 30px);
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive, .MapActive, .ComparisonActive{
      right: 15px;
      top: 14px;
    }
  }
</style>
