import config from '../config'
import arcgisPackage from '@/utils/arcgisTools'
/* 选择 */
export default {
  data () {
    return {
      choiceLayerId: 'orchardLayer', // 被选择图层的Id
      choiceStatus: false,
      originalSelectedGraphics: [], // 选中的图层，用于其他工具使用
      selectedGraphic: {}, // 选中的图层
      choicePM: {}, // 鼠标移动事件
      choiceScores: [], // 绘制的矩形的数据数组
      choiceCutter: {}, // 绘制的矩形
      keyDown: {
        shift: false,
        alt: false
      }
    }
  },
  methods: {
    /* 切换按钮状态 */
    choiceStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools(['choice', 'attribute'])
      if (!this.choiceStatus) {
        this.choiceStatus = true
        this.cursorType = 'choice'
        this.addChoice()
      } else {
        this.choiceStatus = false
        this.delChoice()
      }
    },
    /* 选择 */
    async addChoice () {
      let geometryEngine = await arcgisPackage.geometryEngine
      // 键盘键按下,修改光标样式
      this.choiceKeyDown = this.view.on('key-down', (evt) => {
        if (evt.key === 'Shift') {
          this.keyDown.shift = true
        }
        if (evt.key === 'Alt') {
          this.keyDown.alt = true
        }
        if (this.keyDown.shift && !this.keyDown.alt) {
          this.cursorType = 'choiceAdd'
        } else if (!this.keyDown.shift && this.keyDown.alt) {
          this.cursorType = 'choiceReduce'
        } else {
          this.cursorType = 'choice'
        }
      })
      // 键盘键释放，修改光标样式
      this.choiceKeyUp = this.view.on('key-up', (evt) => {
        if (evt.key === 'Shift') {
          this.keyDown.shift = false
        }
        if (evt.key === 'Alt') {
          this.keyDown.alt = false
        }
        if (this.keyDown.shift && !this.keyDown.alt) {
          this.cursorType = 'choiceAdd'
        } else if (!this.keyDown.shift && this.keyDown.alt) {
          this.cursorType = 'choiceReduce'
        } else {
          this.cursorType = 'choice'
        }
      })
      // 定义地图的按下事件
      this.choiceDown = this.view.on('pointer-down', (evt) => {
        // 禁止移动地图
        this.choiceDrag = this.view.on('drag', (event) => {
          event.stopPropagation()
        })
        // 禁止按住shift后的鼠标拖拽
        this.choiceDragShift = this.view.on('drag', ['Shift'], (event) => {
          event.stopPropagation()
        })
        // 绘制矩形
        this.drawRectangle(evt)
        // 定义地图的拖动事件
        this.choicePM = this.view.on('pointer-move', (evt) => {
          this.updataRectangle(evt)
        })
      })
      // 定义地图的弹起事件
      this.choiceUp = this.view.on('pointer-up', (event) => {
        // 删除地图的拖动事件
        this.choicePM.remove()
        // 删除地图禁止移动
        this.choiceDrag.remove()
        // 删除地图静止shift+拖拽功能
        this.choiceDragShift.remove()
        // 获取选中的几何
        let gras = []
        this.map.findLayerById(this.choiceLayerId).queryFeatures().then((res) => {
          for (let item of res.features) {
            let intersect = geometryEngine.intersects(this.choiceCutter.geometry, item.geometry)
            if (intersect) {
              item.symbol = config.selectionSym
              gras.push(item)
            }
          }
          if (event.native.shiftKey && !event.native.altKey) {
            // 加选 Shift
            let arr = []
            for (let item1 of gras) {
              let gra = true
              for (let item2 of this.originalSelectedGraphics) {
                let equals = geometryEngine.equals(item1.geometry, item2.geometry)
                if (equals) {
                  gra = false
                }
              }
              if (gra) {
                item1.symbol = config.selectionSym
                arr.push(item1)
              }
            }
            gras = this.originalSelectedGraphics.concat(arr)
          } else if (!event.native.shiftKey && event.native.altKey) {
            // 减选 Alt
            let arr = []
            for (let item1 of this.originalSelectedGraphics) {
              let gra = true
              for (let item2 of gras) {
                let equals = geometryEngine.equals(item1.geometry, item2.geometry)
                if (equals) {
                  gra = false
                }
              }
              if (gra) {
                arr.push(item1)
              }
            }
            gras = arr
          }
          this.originalSelectedGraphics = gras
          this.map.findLayerById('sLayer').removeAll()
          this.map.findLayerById('sLayer').addMany(gras)
          // 删除矩形
          this.view.graphics.remove(this.choiceCutter)
        })
        console.log('Graphics:')
        console.log(this.map.findLayerById('sLayer').graphics.items)
        // console.log(JSON.stringify(this.map.findLayerById('sLayer').graphics.items))
      })
    },
    /* 删除选择工具 */
    delChoice () {
      /* 删除鼠标事件 */
      let removes = (rem) => {
        if (rem) {
          rem.remove()
        }
      }
      removes(this.choiceDown)
      removes(this.choiceUp)
      removes(this.choiceKeyDown)
      removes(this.choiceKeyUp)
    },
    /* 在某个点绘制矩形，长宽等于0 */
    async drawRectangle (evt) {
      let Graphic = await arcgisPackage.Graphic
      let point = this.view.toMap({ x: evt.x, y: evt.y })
      this.choiceScores = [[point.x, point.y], [point.x, point.y]]
      let vertices = this.choiceScores
      let rings = [vertices[0], [vertices[0][0], vertices[1][1]], vertices[1], [vertices[1][0], vertices[0][1]]]
      let lineGeom = {
        type: 'polygon',
        rings: rings,
        spatialReference: config.spatialReference
      }
      this.choiceCutter = new Graphic({
        geometry: lineGeom,
        symbol: config.cutterSym
      })
      this.view.graphics.add(this.choiceCutter)
    },
    /* 更新矩形 */
    updataRectangle (evt) {
      let point = this.view.toMap({ x: evt.x, y: evt.y })
      // 将缓存数组最后一个坐标修改为鼠标当前位置
      this.choiceScores[this.choiceScores.length - 1] = [point.x, point.y]
      let vertices = this.choiceScores
      let rings = [vertices[0], [vertices[0][0], vertices[1][1]], vertices[1], [vertices[1][0], vertices[0][1]]]
      // 深度复制cutter到新对象
      let cutterTmp = this.choiceCutter.clone()
      // 修改坐标位置
      cutterTmp.geometry.rings = rings
      // 删除原始矩形
      this.view.graphics.remove(this.choiceCutter)
      // 添加新的矩形
      this.view.graphics.add(cutterTmp)
      this.choiceCutter = cutterTmp
    }
  }
}
