import arcgisPackage from '@/utils/arcgisTools'
import config from '../config'
/* 融合工具 */
export default {
  data () {
    return {
      mergeStatus: false,
      selections: [],
      selectedGraphic: {},
      mergeModal: false, // 属性选择面板开关
      mergeObj: {} // 选中几何的属性
    }
  },
  methods: {
    /* 切换合并按钮状态 */
    mergeStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('merge')
      if (!this.mergeStatus) {
        let sLayer = this.map.findLayerById('sLayer')
        if (!sLayer.graphics.length) {
          setTimeout(() => {
            this.$Modal.warning({
              title: '选择',
              content: '请选择需要融合的图形！'
            })
            // 打开选择工具
            this.activeChoice()
          }, 300)
        } else if (sLayer.graphics.length === 1) {
          setTimeout(() => {
            this.$Modal.error({
              title: '错误',
              content: '请选择至少两个几何进行融合操作！'
            })
            // 打开选择工具
            this.activeChoice()
          }, 300)
        } else {
          this.mergeStatus = true
          this.mergeModal = true
        }
      } else {
        this.map.findLayerById('sLayer').removeAll()
        this.delMerge()
      }
    },
    /* 鼠标点击选中块高亮，选择层样式高亮，显示属性详情 */
    mergeChickList (attr) {
      // 激活点选的几何
      this.mergeObj = attr
      // 高亮地图上选中的几何
      let gras = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        if (item.attributes.ObjectID === attr.ObjectID) {
          gra.symbol = config.attributeSym
        }
        gras.push(gra)
      }
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany(gras)
    },
    /* 合并多边形 */
    async mergeGeoms () {
      let Graphic = await arcgisPackage.Graphic
      let geometryEngine = await arcgisPackage.geometryEngine
      // 融合选中的图形
      let sLayer = this.map.findLayerById('sLayer').graphics.items
      let geos = []
      for (let item of sLayer) {
        geos.push(item.geometry)
      }
      let mergedGeometry = geometryEngine.union(geos)
      this.map.findLayerById('sLayer').removeAll()
      this.newFeature = new Graphic({ geometry: mergedGeometry, symbol: config.orchardSym, attributes: this.mergeObj })
      // 获取地块面积和中心点
      await this.getAreaCenter(this.newFeature)
      this.map.findLayerById('orchardLayer').applyEdits({
        addFeatures: [this.newFeature],
        deleteFeatures: this.originalSelectedGraphics
      })
      // 关闭工具
      this.delMerge()
      // 打开选择工具
      this.activeChoice()
    },
    /* 关闭工具 */
    delMerge () {
      this.mergeStatus = false
      this.mergeModal = false
    }
  }
}
