<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title="新建数据"
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="name" label="数据名称">
          <Input v-model.trim="AddData.name" placeholder="请输入数据名称" size="default"/>
        </FormItem>
        <FormItem prop="serviceUrl" label="数据地址">
          <Input v-model.trim="AddData.serviceUrl" placeholder="请输入数据地址" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="备注">
          <Input v-model.trim="AddData.remark" placeholder="请输入备注" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading1" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { addShp } from '@/api/adjustment'
export default {
  name: 'mobileUserAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {
        serviceUrl: '',
        name: '',
        remark: ''
      },
      AddRule: {
        serviceUrl: [
          { required: true, message: '数据地址不能为空', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '数据名称不能为空', trigger: 'blur' }
        ]
      },
      /* 新建框 */
      mobileUserBuild: false,
      /* 加载 */
      loading1: false
    }
  },
  props: {},
  methods: {
    /* axios: 添加数据 */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          this.loading1 = true
          addShp({
            data: {
              serviceUrl: this.AddData.serviceUrl,
              name: this.AddData.name,
              remark: this.AddData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.loading1 = false
              this.$emit('getNewList')
              this.$Notice.success({ title: '新建数据成功' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {
        serviceUrl: '',
        name: '',
        remark: ''
      }
      this.$refs.add.resetFields()
      this.loading1 = false
    }
  }
}
</script>

<style scoped lang="scss">
</style>
